import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import './App.css';
import Earth from './components/pages/Earth';
import Home from './components/pages/Home';
import Jupiter from './components/pages/Jupiter';
import Mars from './components/pages/Mars';
import Mercury from './components/pages/Mercury';
import Neptune from './components/pages/Neptune';
import Saturn from './components/pages/Saturn';
import Uranus from './components/pages/Uranus';
import Venus from './components/pages/Venus';


function App() {
  return (
    <Router className="App">
      <Switch>
        <Route path='/planets' component={Home} exact/>
        <Route path='/planets/mercury' component={Mercury} exact />
        <Route path='/planets/venus' component={Venus} exact />
        <Route path='/planets/earth' component={Earth} exact />
        <Route path='/planets/mars' component={Mars} exact />
        <Route path='/planets/jupiter' component={Jupiter} exact />
        <Route path='/planets/saturn' component={Saturn} exact />
        <Route path='/planets/uranus' component={Uranus} exact />
        <Route path='/planets/neptune' component={Neptune} exact />
      </Switch>
    </Router>
  );
}

export default App;
