import React from "react";
import { Link } from "react-router-dom";
import "./planets.scss";
import { FaArrowLeft, FaLessThan } from "react-icons/fa";

const PrevPlanet = ({ data }) => {
  const [hover, setHover] = React.useState(false);
  const handleHover = () => setHover(false);
  const handleHoverOut = () => setHover(true);

  return (
    <Link to={data.prevPlanetUrl}>
      <div className="prev_planet"  onMouseEnter={handleHover} onMouseLeave={handleHoverOut}>
        <i style={{ color: hover ? "#fff" : "#d46045" }}>
          {hover ? <FaLessThan /> : < FaArrowLeft/>}
        </i>
        <div className="image-box">
          <img src={data.prevPlanetImage} alt={data.prevPlanetName} />
        </div>
        <h4 style={{color: hover ? "#fff" : "#d46045" }}>{data.prevPlanetName}</h4>
      </div>
    </Link>
  );
};

export default PrevPlanet;
