import React from "react";
import Navbar from "../Navbar";
import "../planetsComponents/planets.scss";
import {Planets} from '../Data'
import PlanetInfo from "../planetsComponents/PlanetInfo";
import NextPlanet from "../planetsComponents/NextPlanet";
import PrevPlanet from "../planetsComponents/PrevPlanet";
// import InfoAsteroid from "../planetsComponents/InfoAsteroid";
import Container from "../planetsComponents/Container";

const data = Planets[0];

const Mercury = () => {

  return (
    <div className="big_body" >
      <Navbar />

      <Container data={data}/>
      
      {/* <InfoAsteroid data={data} /> */}
      
      <PlanetInfo data={data} />

      <NextPlanet data={data} />

      <PrevPlanet data={data} />
    </div>
  );
};

export default Mercury;
