import React from "react";
import "./planets.scss";

const PlanetInfo = ({ data }) => {
  return (
    <div className="planet_info">
      <h3>Moons</h3>
      <p>{data.moons}</p>
      <h3>Radius</h3>
      <p>{data.radius}</p>
      <h3>Distance from Sun</h3>
      <p>{data.sunDistance}</p>
      <h3>Orbital Period</h3>
      <p>{data.orbitalPeriod}</p>
    </div>
  );
};

export default PlanetInfo;
