import React, {useState} from "react";
import { Link } from "react-router-dom";
// import { Button } from "../Button/Button";
import "./Navbar.css";

const Navbar = () => {
  const [click, setClick] = useState(false);
  // const [button, setButton] = useState(false);

  const handleClick = () => setClick(!click);
  const closeMobileMenu = () => setClick(false);

  // const showButton = () => {
  //   if (window.innerWidth <= 960) {
  //     setButton(false);
  //   } else {
  //     setButton(true);
  //   }
  // };

  // useEffect(() => {
  //   showButton();
  // }, []);

  // window.addEventListener("resize", showButton);

  return (
    <>
      <nav className="navbar">
        <div className="navbar_container">
          <Link
            to="/planets"
            className="navbar_logo"
            onClick={closeMobileMenu}
          >
            <img
              src={require("../components/images/logo-sistema-solar.png").default}
              alt="Sistema Solar"
            />
            
          </Link>
          <div className="menu_icon" onClick={handleClick}>
            <i className={click ? "fas fa-times" : "fas fa-bars"} />
          </div>
          <ul className={click ? 'nav_menu active' : 'nav_menu' }>
              <li className='nav_items'>
                  <Link to='/planets/mercury' className='nav_links' onClick={closeMobileMenu} exact="true">
                    Mercury
                  </Link>
              </li>
              <li className='nav_items'>
                  <Link to='/planets/venus' className='nav_links' onClick={closeMobileMenu}>
                    Venus
                  </Link>
              </li>
              <li className='nav_items'>
                  <Link to='/planets/earth' className='nav_links' onClick={closeMobileMenu}>
                    Earth
                  </Link>
              </li>
              <li className='nav_items'>
                  <Link to='/planets/mars' className='nav_links' onClick={closeMobileMenu}>
                    Mars
                  </Link>
              </li>
              <li className='nav_items'>
                  <Link to='/planets/jupiter' className='nav_links' onClick={closeMobileMenu}>
                    Jupiter
                  </Link>
              </li>
              <li className='nav_items'>
                  <Link to='/planets/saturn' className='nav_links' onClick={closeMobileMenu}>
                    Saturn
                  </Link>
              </li>
              <li className='nav_items'>
                  <Link to='/planets/uranus' className='nav_links' onClick={closeMobileMenu}>
                    Uranus
                  </Link>
              </li>
              <li className='nav_items'>
                  <Link to='/planets/neptune' className='nav_links' onClick={closeMobileMenu}>
                    Neptune
                  </Link>
              </li>
              {/* <li className='nav_items'>
                  <Link to='' className='nav_links_mobile' onClick={closeMobileMenu}>
                    More Projects
                  </Link>
              </li> */}
          </ul>
          {/* {button && <Link>More Projects</Link>} */}
        </div>
      </nav>
    </>
  );
};

export default Navbar;
