import React from "react";
import "./planets.scss";
import { FaArrowRight, FaGreaterThan } from "react-icons/fa";
import { Link } from "react-router-dom";

const NextPlanet = ({ data }) => {

    const [hover, setHover] = React.useState(false);
    const handleHover = () => setHover(false);
    const handleHoverOut = () => setHover(true);

  return (
    <Link to={data.nextPlanetUrl}>
      <div className="next_planet" onMouseEnter={handleHover} onMouseLeave={handleHoverOut}>
        <i style={{color: hover ? "#fff" : "#d46045" }}>
          {hover ? <FaGreaterThan /> : <FaArrowRight />}
        </i>
        <div className="image-box">
          <img src={data.nextPlanetImage} alt={data.nextPlanetName} />
        </div>
        <h4 style={{color: hover ? "#fff" : "#d46045" }}>{data.nextPlanetName}</h4>
      </div>
    </Link>
  );
};

export default NextPlanet;
